      <section class="chess-notepad__full-instructions" hidden>
        <h3>How the Clock Works</h3>
        <p>Clocks are set as "N minutes | N increment seconds". The increment is added after each move.</p>
        <p>The screen is divided in half. Black taps his or her clock to start the other clock and the game.</p>

        <h3>How to Notate Your Moves</h3>
        <p>This app is meant to guide you in notation while also teaching you how to do it. Each player will get a serious of buttons for notation once they've made their move and stopped their clock.</p>

        <h4>Step 1: Note the piece you moved</h4>
        <p>The first step is to note the piece you moved. Tap the standard letter of the piece:</p>

        <ul>
          <li>"K" = King</li>
          <li>"Q" = Queen</li>
          <li>"B" = Bishop</li>
          <li>"N" = Knight</li>
          <li>"R" = Rook</li>
          <li>" " = Pawn</li>
        </ul>

        <p>Knight's are known as N since K is reserved for the king. If a letter is not given that indicates a pawn. That's what the blank button is for.</p>

        <h4>Steps 2 & 3: Note the square you moved to</h4>
        <p>The board is composed of files (columns) and ranks (rows). The files</p>

